.scroll-center {
    position: absolute;
    margin-top: 150px;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%)
}

.scroll-center-m {
    position: absolute;
    margin-top: 150px;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%)
}

.scroll-down {
    position: relative;
    width: 30px;
    height: 50px;
    border-radius: 25px;
    background: transparent;
    border: 2px solid white;
    overflow: hidden;
}

.scroll-down:before {
    content: '';
    position: absolute;
    width: 3px;
    height: 6px;
    border-radius: 50%;
    background: white;
    left: 50%;
    top: 10px;
    -webkit-transform: translate(-50%, 0);
        -ms-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    -webkit-animation: animate 1s linear infinite;
            animation: animate 1s linear infinite;
}

.scroll-down:after {
    content: '';
    position: absolute;
    width: 3px;
    height: 15px;
    border-radius: 50%;
    background: white;
    left: 50%;
    top: 5px;
    -webkit-transform: translate(-50%, 0);
        -ms-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    border-radius: 1px;
}

@-webkit-keyframes animate {
    0%
    {
        -webkit-transform: translate(-50%,20px);
                transform: translate(-50%,20px);
        opacity: 1;
    }
    80%
    {
        -webkit-transform: translate(-50%,60px);
                transform: translate(-50%,60px);
        opacity: 1;
    }
    100%
    {
        -webkit-transform: translate(-50%,100px);
                transform: translate(-50%,100px);
        opacity: 1;
    }
}

@keyframes animate {
    0%
    {
        -webkit-transform: translate(-50%,20px);
                transform: translate(-50%,20px);
        opacity: 1;
    }
    80%
    {
        -webkit-transform: translate(-50%,60px);
                transform: translate(-50%,60px);
        opacity: 1;
    }
    100%
    {
        -webkit-transform: translate(-50%,100px);
                transform: translate(-50%,100px);
        opacity: 1;
    }
}

.scroll-down-text {
    color: white;
    margin-top: 200px !important;
}

.scroll-down-text-m {
    color: white;
    margin-top: 200px !important;
}


